let changelook= ['韩府', '娱乐',"二手","周边"];
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
import uploudImages from "../../components/common/uploud-images.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue";
import {mapState} from 'vuex'
export default {
	components:{
		uniNavBar,
		uploudImages,
		uniPopup
	},
	data() {
		return {
			isget:false,
			showpopup:false,
			yinsi:"韩府",
			cid:1,
			text:"",
			imglist:[],
			files: []
		};
	},
	onBackPress() {
		// 如果有值
		if(!this.text && this.imglist.length<1){ return; }
		if(!this.isget){
			this.baocun();
			return true;
		}
	},
	async onLoad() {
		let data = await this.$http.get('/category/list')
		changelook = data
	},
	computed:{
		...mapState(['userInfo'])
	},
	methods:{
		// 保存为草稿
		baocun(){
			uni.showModal({
				content: '是否要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isget=true;
					uni.navigateBack({
						delta: 1
					});
				},
			});
		},
		// 返回
		back(){
			uni.navigateBack({ delta: 1 });
		},
		// 发布
		async submit(){
			console.log("发布")
			if(this.text==''&&this.files.length==0){
				return
			}
			let images = []
			for(let i=0;i<this.files.length;i++){
				let url = await this.$http.uploudFile("/upload/cloud",this.files[i])
				images.push(url);
					console.log(i)
			}

			await this.$http.post("/topic",{
				uid:this.userInfo.id,
				cid:this.cid,
				title:this.text,
				urlType:"img",
				images: images.join(',')
			})

			
			uni.showToast({
				title: '发表成功',
				icon:"success",
				duration: 200,
				success:()=>{
					this.text = '',
					this.files.length=0
					this.imglist.length=0
					
					uni.navigateBack({
						delta:10
					})
				}
			});
			
		},
		// 隐私
		changelook(){
			uni.showActionSheet({
				itemList:changelook.map((item)=>item.name),
				success: (res)=> {
					this.yinsi=changelook[res.tapIndex].name
					this.cid =changelook[res.tapIndex].id
				}
			});
		},
		uploud(arr, files){
			this.imglist=arr;
			this.files = files
			
			console.log(this.imglist)
		},
		hidePopup(){
			this.showpopup=false;
		}
	}
}
